const formatMoney = (number) => {
  const pos = number < 0;
  number = Math.abs(number);
  return (pos ? '-' : '') + '$' + new Intl.NumberFormat('ru-RU', { style: 'currency', currency: 'USD' }).format(number).slice(0, -2);
};

export const usdToRub = (dollar, dollarRate) => {
  return Math.round(dollar*dollarRate*100)/100 || 0;
};

export const formatMoneyRub = (rub) => {
  return new Intl.NumberFormat('ru-ru', { style: 'currency', currency: 'RUB' }).format(rub);
};

export function cc_format(value) {
  const v = value.replace(/\s+/g, '').replace(/[^0-9]/gi, '');
  const matches = v.match(/\d{4,18}/g);
  const match = (matches && matches[0]) || '';
  const parts = [];

  for (let i=0; i<match.length; i+=4) {
    parts.push(match.substring(i, i+4))
  }

  if (parts.length) {
    return parts.join(' ');
  } else {
    return value;
  }
}

export default formatMoney;
