import Wrapper from "../../../../common/core/Wrapper";
import React, {useContext, useState} from "react";
import {Text, RadioGroup, Radio} from 'informed';
import AutoForm from "../../../../common/core/AutoForm";
import s from "./s.module.scss";
import Button from "../../../../common/core/Button";
import useWaiting from "../../../../common/hooks/useWaiting";
import cn from 'classnames';
import DataContext from "../../../../contexts/DataContext";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import moment from "moment";

const Input = ({header, ...props}) => {
  return (
    <Col xs={6} lg={4} className={s.inputContainer}>
      <div className={s.inputHeader}>{header}</div>
      <Text className={cn(s.inputElement, s.shadow)} required {...props}/>
    </Col>
  );
};

const RadioItem = ({value, label}) => (
  <div className={s.radioItem}>
    <Radio className={s.radio} value={value}/>
    {label}
  </div>
);

const Profile = () => {
  const [formApi, setFormApi] = useState({});
  const {user} = useContext(DataContext);
  const {verified} = user.data;

  const submit = async (formState) => {
    await user.set(formState);
  };

  const [waiting, onSubmit] = useWaiting(submit);

  return (
    <Wrapper className={s.container}>
      <div className={s.status}>
        Статус: <span className={cn(s.statusText, verified && s.verified)}>{
          verified ? 'ВЕРИФИЦИРОВАННЫЙ' : 'АНОНИМНЫЙ'
        }</span>
      </div>
      <div className={s.editable}>
        <AutoForm
          onSubmit={onSubmit}
          formApi={formApi}
          setFormApi={setFormApi}
          autoComplete={'off'}
          initialValues={user.data}
        >
          <div className={s.header}>Личные данные</div>
          <Row>
            <Input
              header={'Имя'}
              field={'firstName'}
            />
            <Input
              header={'Фамилия'}
              field={'secondName'}
            />
            <Input
              header={'Email'}
              field={'email'}
            />
            <Input
              header={'Дата рождения'}
              field={'birthDate'}
              parse={(value) => {
                return moment(value).format("YYYY-MM-DDTHH:00:00.000+00:00");
              }}
              format={(value) => moment(value).format("YYYY-MM-DD")}
              type={'date'}
            />
            <Input
              header={'Место рождения'}
              field={'birthPlace'}
            />
            <Input
              header={'Гражданство (подданство)'}
              field={'citizenship'}
            />
          </Row>
          <div className={s.header}>Место жительства (регистрации)</div>
          <Row>
            <Input
              header={'Страна'}
              field={'country'}
            />
            <Input
              header={'Населенный пункт'}
              field={'city'}
            />
            <Input
              header={'Улица'}
              field={'street'}
            />
            <Input
              header={'Дом, строение'}
              field={'building'}
            />
            <Input
              header={'Квартира (офис)'}
              field={'flat'}
            />
          </Row>
          <div className={s.header}>Возраст</div>
          <RadioGroup field={'age'} required>
            <RadioItem value={'under20'} label={'до 20 лет'}/>
            <RadioItem value={'from20to50'} label={'от 20 до 50 лет'}/>
            <RadioItem value={'over50'} label={'старше 50 лет'}/>
          </RadioGroup>
          <div className={s.header}>
            Соотношение примерных среднемесячных доходов и среднемесячных расходов за последние 12 месяцев
          </div>
          <RadioGroup field={'earningMoney'} required>
            <RadioItem value={'yes'} label={'среднемесячные доходы больше среднемесячных расходов'}/>
            <RadioItem value={'no'} label={'среднемесячные доходы меньше среднемесячных расходов'}/>
          </RadioGroup>
          <div className={s.header}>
            Сбережения
          </div>
          <RadioGroup field={'savings'} required>
            <RadioItem value={'more'} label={'сбережения превышают стоимость активов,  инвестируемых в фонд'}/>
            <RadioItem value={'notMore'} label={'сбережения не превышают стоимость активов,  инвестируемых в фонд'}/>
          </RadioGroup>
          <div className={s.header}>Опыт (знания) Клиента в области инвестирования</div>
          <RadioGroup field={'experience'} required>
            <RadioItem value={'from3'} label={'большой (от 3 лет)'}/>
            <RadioItem value={'from1to3'} label={'средний (от 1 до 3 лет)'}/>
            <RadioItem value={'under1'} label={'незначительный (до 1 года)'}/>
            <RadioItem value={'none'} label={'отсутствует'}/>
          </RadioGroup>
          <div className={s.header}>Инвестиционные цели Клиента</div>
          <RadioGroup field={'purpose'} required>
            <RadioItem value={'protect'} label={'защита капитала (минимум риска)'}/>
            <RadioItem value={'smallProfit'} label={'получение дохода (небольшие риски)'}/>
            <RadioItem value={'mediumProfit'} label={'получение существенного дохода (периодические риски)'}/>
            <RadioItem value={'maxProfit'} label={'получение максимального дохода (значительные риски)'}/>
          </RadioGroup>
          <Button
            containerClassName={s.buttonContainer}
            className={s.button}
            title={'СОХРАНИТЬ'}
            color={'white'}
            borderColor={'#42287D'}
            waiting={waiting}
            waitingColor={'rgba(255,255,255,0.35)'}
            backgroundColor={'#2C5BCA'}
          />
        </AutoForm>
      </div>
    </Wrapper>
  );
};

export default Profile;
