import {GET, POST} from "./network";

export async function loginUser({email, password}) {
  return await POST('/api/auth/login', {email, password});
}

export async function registerUser(obj) {
  return await POST('/api/auth/register', obj);
}

export async function checkAuth() {
  return await GET('/api/auth/checkAuth');
}

export async function getAccountData() {
  return await GET('/api/user/getData');
}

export async function updateAccountData(obj) {
  return await POST('/api/user/update', obj);
}

export async function requestPurchase(obj) {
  return await POST('/api/investment/requestPurchase', obj);
}

export async function requestSale(id) {
  return await GET(`/api/investment/requestSale/${id}`);
}

export async function getAllProducts() {
  return await GET('/api/product/getAll') || [];
}

export async function getDepositEmail() {
  return await GET('/api/user/deposit');
}

export async function getWithdrawEmail() {
  return await GET('/api/user/withdraw');
}

export async function getSettings() {
  return await GET('/api/settings/get');
}

export async function forgotPassword(data) {
  return await POST('/api/auth/forgotPassword', data);
}

