import {useReducer, createContext} from "react";
import Enum from "../utils/enum";
import {
  getAccountData,
  getAllProducts, getSettings,
  updateAccountData,
} from "../api/requests";
import {ListField, StateField, UserField} from "./utils/fields";

const [PRODUCTS, USER, SETTINGS] = Enum(3);

const contextShape = {
  products: ListField.default,
  user: StateField.default,
  settings: StateField.default,
};

const DataContext = createContext(contextShape);

function reducer(state, action) {
  switch (action.type) {
    case PRODUCTS:
      return {...state, products: action.products};
    case USER:
      return {...state, user: action.user};
    default:
      return state;
  }
}

export const ProductType = {
  "PORTFEL": 1,
  "FUND": 2
};

export const InvestmentStatus = {
  "UNCONFIRMED_PURCHASE": 1,
  "ACTIVE_INVESTMENT": 2,
  "UNCONFIRMED_SALE": 3,
  "CONFIRMED_SALE": 4
};

export function useApplicationData() {
  const products = new ListField({
    listGetter: getAllProducts,
    resolver: (products) => dispatch({type: PRODUCTS, products})
  });

  const user = new UserField({
    getter: getAccountData,
    setter: updateAccountData,
    resolver: (user) => dispatch({type: USER, user})
  });

  const settings = new StateField({
    getter: getSettings,
    resolver: (settings) => dispatch({type: SETTINGS, settings})
  });

  const initialState = {user, products, settings};

  const [state, dispatch] = useReducer(reducer, initialState);

  return state;
}

export default DataContext;
