import TouchableOpacity from "../../../../../common/core/TouchableOpacity";
import s from "../styles/s.module.scss";
import cn from "classnames";
import React, {useContext} from "react";
import DataContext, {InvestmentStatus, ProductType} from "../../../../../contexts/DataContext";
import useWaiting from "../../../../../common/hooks/useWaiting";
import {BounceLoader} from "react-spinners";

const ActionCell = ({investment, productTypeId, canSell, setModalOpen}) => {
  const {id, statusId} = investment;
  const {user} = useContext(DataContext);
  const text = productTypeId === ProductType.PORTFEL ? 'Продать портфель' : 'Вывести средства';

  const [waiting, requestSale] = useWaiting(user.requestSale);

  const sale = async () => {
    await requestSale(id);
    setModalOpen(true);
  };

  if (!user.data.verified) {
    return <div className={cn(s.action, s.inactive)}>Не верифицирован</div>
  }

  return statusId === InvestmentStatus.ACTIVE_INVESTMENT ? (
    <>
      {canSell ?
        <TouchableOpacity
          className={s.action}
          onClick={sale}
        >
          {waiting ? <BounceLoader color={'#e81c1c'} size={20}/> : text}
        </TouchableOpacity>
        :
        <div
          title={`Вы не можете ${text.toLowerCase()} раньше истечения срока`}
          className={cn(s.action, s.inactive)}
        >
          {text}
        </div>
      }
    </>
  ) : <div className={cn(s.action, s.processing)}>Обрабатывается</div>;
};

export default ActionCell;
