import {useState, useEffect, useRef} from 'react';

export default function useMouseDown(ref) {
  const [pressing, setPressing] = useState(false);

  useEffect(() => {
    if (!ref) return;
    const el = ref.current;
    el.addEventListener('mousedown', onMouseDown);
    el.addEventListener('touchstart', onTouchStart);
    return () => {
      el.removeEventListener('mousedown', onMouseDown);
      el.addEventListener('touchstart', onTouchStart);
    };
  });

  const onTouchStart = () => {
    setPressing(true);
    document.addEventListener('touchend', onDocumentTouchEnd);
    document.addEventListener('dragend', onDocumentTouchEnd);
  };

  const onDocumentTouchEnd = () => {
    setPressing(false);
    document.removeEventListener('touchend', onDocumentTouchEnd);
    document.removeEventListener('dragend', onDocumentTouchEnd);
  };

  const onMouseDown = () => {
    setPressing(true);
    document.addEventListener('mouseup', onDocumentMouseUp);
    document.addEventListener('dragend', onDocumentMouseUp);
  };

  const onDocumentMouseUp = () => {
    setPressing(false);
    document.removeEventListener('mouseup', onDocumentMouseUp);
    document.removeEventListener('dragend', onDocumentMouseUp);
  };

  return pressing;
}