import React, {useState, useRef, forwardRef, useEffect} from "react";
import Tabs from "./Tabs";
import Wrapper from "../../common/core/Wrapper";
import styles from './style.module.css';
import cn from 'classnames';
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import animate, {easeInOut} from "../../utils/animate";
import CustomButton from "../../common/core/Button";
import Services from "./contents/Services";
import OpenAccount from "./contents/OpenAccount";
import Contacts from "./contents/Contacts";
import Footer from "./Footer";
import About from "./contents/About";
import Login from "./Auth/Login";
import Register from "./Auth/Register";
import getRefRect from "../../utils/getRefRect";
import {BounceLoader} from "react-spinners";
import './preload.css';

const TAB_SLIDE_DURATION = 500;

const MAIN = 0;
const LOGIN = 4;
const REGISTER = 5;

const Landing = ({defaultTabIndex}) => {
  const [activeTabIndex, setActiveTabIndex] = useState(defaultTabIndex);
  useEffect(() => {
    if (defaultTabIndex === LOGIN) {
      tabsInfo[LOGIN].forceAction();
    }
  }, [defaultTabIndex]);

  const tabsInfo = [{
    content: 'ГЛАВНАЯ',
    tabClassName: '',
    action: () => {
      slideToMain();
    },
  },{
    content: 'О НАС',
    tabClassName: 'd-none d-lg-block',
    action: () => {
      scrollToRef(aboutRef);
    },
  },{
    content: 'УСЛУГИ',
    dropDown: [{
      name: 'Инвестиционный портфель',
      action: () => {
        setServiceModal({name: 'portfels'});
      },
    },{
      name: 'Наш фонд',
      action: () => {
        setServiceModal({name: 'funds'});
      },
    }],
    tabClassName: 'd-none d-md-block',
    action: () => {
      scrollToRef(servicesRef);
    },
  },{
    content: 'КОНТАКТЫ',
    tabClassName: 'd-none d-lg-block',
    action: () => {
      scrollToRef(contactsRef);
    },
  },{
    content: 'ВОЙТИ',
    action: () => {
      slideToLogin();
    },
    forceAction: () => {
      setRefsToLeft(loginRef, registerRef);
      slideRefsToRight(mainRef, authRef);
    },
  },{
    content: 'ОТКРЫТЬ СЧЕТ',
    action: () => {
      slideToRegister();
    },
  }];

  const [serviceModal, setServiceModal] = useState({name: null});

  const aboutRef = useRef(null);
  const servicesRef = useRef(null);
  const accountRef = useRef(null);
  const contactsRef = useRef(null);

  const scrollToRef = (ref) => {
    return new Promise(resolve => {
      const fromTop = window.scrollY;
      const toTop = fromTop + getRefRect(ref).top;
      animate({
        duration: TAB_SLIDE_DURATION,
        timing: easeInOut,
        draw: (progress) => {
          const y = fromTop + (toTop-fromTop)*progress;
          window.scrollTo(0, y);
        },
        then: resolve
      });
    });
  };

  const slidingContainerRef = useRef(null);
  const mainRef = useRef(null);
  const authRef = useRef(null);
  const loginRef = useRef(null);
  const registerRef = useRef(null);

  const slidingInProgress = useRef(false);
  const onSlideStart = () => slidingInProgress.current = true;
  const onSlideEnd = () => slidingInProgress.current = false;

  const slideToMain = () => {
    if (activeTabIndex === MAIN || slidingInProgress.current) return;
    setActiveTabIndex(MAIN);
    slideRefsToLeft(mainRef, authRef);
  };

  const slideToLogin = (force) => {
    if ((activeTabIndex === LOGIN || slidingInProgress.current)) return;

    if (activeTabIndex === MAIN) {
      setRefsToLeft(loginRef, registerRef);
      slideRefsToRight(mainRef, authRef);
    } else if (activeTabIndex === REGISTER) {
      slideRefsToLeft(loginRef, registerRef);
    }

    setActiveTabIndex(LOGIN);
  };

  const slideToRegister = () => {
    if (activeTabIndex === REGISTER || slidingInProgress.current) return;

    if (activeTabIndex === MAIN) {
      setRefsToRight(loginRef, registerRef);
      slideRefsToRight(mainRef, authRef);
    } else if (activeTabIndex === LOGIN) {
      slideRefsToRight(loginRef, registerRef);
    }

    setActiveTabIndex(REGISTER);
  };

  const setRefsToRight = (leftRef, rightRef) => {
    leftRef.current.style.left = -100 + '%';
    rightRef.current.style.left = 0 + '%';
  };

  const setRefsToLeft = (leftRef, rightRef) => {
    leftRef.current.style.left = 0 + '%';
    rightRef.current.style.left = 100 + '%';
  };

  const slideRefsToRight = (leftRef, rightRef) => {
    onSlideStart();
    animate({
      duration: TAB_SLIDE_DURATION,
      timing: easeInOut,
      draw: (progress) => {
        leftRef.current.style.left = -progress*100 + '%';
        rightRef.current.style.left = 100-100*progress + '%';
      },
      then: onSlideEnd,
    });
  };

  const slideRefsToLeft = (leftRef, rightRef) => {
    onSlideStart();
    animate({
      duration: TAB_SLIDE_DURATION,
      timing: easeInOut,
      draw: (progress) => {
        leftRef.current.style.left = -100+progress*100 + '%';
        rightRef.current.style.left = 100*progress + '%';
      },
      then: onSlideEnd,
    });
  };

  const [registerInitialValues, setRegisterInitialValues] = useState({});

  const onOpenAccountSubmit = (formState) => {
    setRegisterInitialValues({...formState});
    scrollToRef(mainRef);
    slideToRegister();
  };

  const headerRef = useRef(null);

  return (
    <div className={styles.landingContainer}>
      <Wrapper style={{minHeight: '100%'}}>
        <Container className={styles.header} ref={headerRef}>
          <Row className={styles.row}>
            <Logo onClick={slideToMain}/>
            <Tabs
              tabsInfo={tabsInfo}
              slideDuration={TAB_SLIDE_DURATION}
              defaultTabIndex={activeTabIndex}
              headerRef={headerRef}
            />
          </Row>
        </Container>

        <Wrapper className={styles.slidingContainer} ref={slidingContainerRef}>
          <Wrapper className={cn(styles.main)} ref={mainRef}>
            <Main onOpenAccount={slideToRegister}/>
          </Wrapper>
          <Wrapper className={cn(styles.auth)} ref={authRef}>
            <Wrapper className={cn(styles.authInnerContainer)}>

              <Wrapper className={cn(styles.login)} ref={loginRef}>
                <Login/>
              </Wrapper>

              <Wrapper className={cn(styles.register)} ref={registerRef}>
                <Register onContinue={slideToLogin} initialValues={registerInitialValues}/>
              </Wrapper>

            </Wrapper>
          </Wrapper>
        </Wrapper>
      </Wrapper>

      <div className={styles.content}>
        <ContentContainer ref={aboutRef}>
          <ContentUnit
              header={'О НАС'}
              content={<About/>}
          />
        </ContentContainer>
        <ContentContainer ref={servicesRef} className={styles.servicesContainer}>
          <ContentUnit
              header={'УСЛУГИ'}
              content={<Services openModal={serviceModal}/>}
          />
        </ContentContainer>
        <ContentContainer ref={accountRef}>
          <ContentUnit
              header={'ОТКРЫТЬ СЧЕТ'}
              content={<OpenAccount onSubmit={onOpenAccountSubmit}/>}
          />
        </ContentContainer>
        <ContentContainer ref={contactsRef} className={styles.contactsContainer}>
          <ContentUnit
              header={'КОНТАКТЫ'}
              content={<Contacts/>}
          />
        </ContentContainer>
        <ContentContainer className={styles.footerContainer}>
          <Footer/>
        </ContentContainer>
      </div>

    </div>
  );
};

const ContentContainer = forwardRef(({children, className, ...props}, ref) => (
  <div ref={ref} className={cn(styles.contentUnitContainer, className)} {...props}>{children}</div>
));

const Main = ({onOpenAccount}) => (
  <>
    <Logo big/>
    <div className={styles.subTitle}>ЛУЧШИЕ ИНВЕСТИЦИОННЫЕ РЕШЕНИЯ</div>
    <div className={styles.separator}/>
    <CustomButton onClick={onOpenAccount} title={'ОТКРЫТЬ СЧЕТ'}/>
  </>
);

const ContentUnit = ({className, header, content}) => (
  <Container className={cn(styles.unit, className)}>
    <div className={styles.unitHeader}>{header}</div>
    <div className={styles.unitSeparator}/>
    {content}
  </Container>
);

export const Logo = ({big, hideSm=true, onClick, style}) => (
  <div className={cn(styles.logo, big && styles.big, !big && hideSm && 'd-none d-lg-block')} onClick={onClick}>
    <span className={styles.nash}>НАШ</span>&nbsp;ФОНД
  </div>
);

// const LoginTabContent = () => (
//   <div className={styles.loginTab}>
//     <Image width={18} className={styles.icon} src={loginIconPath}/>
//     ВОЙТИ
//   </div>
// );

export const Loading = () => (
  <Wrapper className={cn('d-flex align-items-center justify-content-center', styles.loading)} style={{position: 'relative'}}>
    <div style={{position: 'absolute', left: '50%', top: '50%', transform: 'translate(-50%, -50%)'}}>
      <BounceLoader color={'rgba(255,255,255,0.2)'} size={300}/>
    </div>
  </Wrapper>
);

export default Landing;
