import TableHeader from "./TableHeader";
import s from "../styles/s.module.scss";
import moment from "moment";
import formatMoney from "../../../../../utils/formatMoney";
import React, {useContext, useState} from "react";
import ReactTable from "react-table";
import Pagination from "./Pagination";
import DataContext, {InvestmentStatus} from "../../../../../contexts/DataContext";

const operationTypes = {
  purchase: 'Покупка',
  sale: 'Продажа',
};

const operationColumns = [{
  Header: <TableHeader name={'НАЗВАНИЕ'}/>,
  Cell: ({value}) => <span className={s.name}>{value}</span>,
  accessor: 'productName',
  width: 180,
},{
  Header: <TableHeader name={'ДАТА'}/>,
  Cell: ({value}) => moment(value).format('DD.MM.YYYY'),
  accessor: 'date',
  width: 150,
},{
  Header: <TableHeader name={'ОПЕРАЦИЯ'}/>,
  // Cell: ({value}) => value,
  accessor: 'operationType',
  width: 150,
},{
  Header: <TableHeader name={'СУММА'}/>,
  Cell: ({value}) => formatMoney(value),
  accessor: 'sum',
  width: 150,
}];

function createOperationsData({user, products}) {
  const {investments=[]} = user.data;
  const findProductById = (id) => products.data.find((item) => item.id === id) || {};

  const data = investments.map((investment) => {
    const {initSum, currentSum, updatedAt, createdAt, statusId, productId} = investment;
    const {name: productName, typeId} = findProductById(productId);

    let operationType, date, sum;
    if (statusId === InvestmentStatus.UNCONFIRMED_PURCHASE || statusId === InvestmentStatus.ACTIVE_INVESTMENT) {
      operationType = 'Покупка';
      date = createdAt;
      sum = initSum;
    } else {
      operationType = 'Продажа';
      date = updatedAt;
      sum = currentSum;
    }

    return {
      operationType, date, sum, productName
    };
  });

  return data;
}

const OperationsTable = ({pageSize}) => {
  const {user, products} = useContext(DataContext);
  const [page, setPage] = useState(0);

  // todo
  const operationHistory = createOperationsData({user, products});
  console.log(operationHistory);

  return (
    <ReactTable
      className={'table'}
      data={operationHistory}
      columns={operationColumns}
      sortable={false}
      resizable={false}
      pageSize={pageSize}
      minRows={0}
      page={page}
      showPageSizeOptions={false}
      PaginationComponent={(props) => (
        props.pages > 1 && <Pagination setPage={setPage} {...props}/>
      )}
      NoDataComponent={() => null}
    />
  );
};

export default OperationsTable;
