import React from 'react';
import Row from 'react-bootstrap/Row';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import styles from './styles.module.scss';
import Image from "react-bootstrap/Image";

const items = [{
  text: 'Безупречный уровень сервиса и компетенция сотрудников.',
  icon: require('./images/1.png'),
},{
  text: 'Высококлассная аналитическая и техническая поддержка.',
  icon: require('./images/2.png'),
},{
  text: 'Эксклюзивные инвестиционные сервисы.',
  icon: require('./images/3.png'),
}];

function Advantages() {
  return (
    <div>
      <Container>
        <Row>
          {items.map(Item)}
        </Row>
      </Container>
    </div>
  );
}

function Item({text, icon}) {
  return (
    <Col xs={12} lg={4} className={styles.item}>
      <Image src={icon} className={styles.icon}/>
      <div className={styles.text}>{text}</div>
    </Col>
  );
}

export default Advantages;
