import React, {useContext, useMemo, useState} from 'react';
import s from './s.module.scss';
import Wrapper from "../../common/core/Wrapper";
import Menu from "./Menu";
import ElectByIndex from "../../common/core/ElectByIndex";
import Bar from "./Bar";
import Products from "./content/Products";
import Investitions from "./content/Investitions";
import Deposit from "./content/Deposit";
import Profile from "./content/Profile";
import DataContext from "../../contexts/DataContext";
import {fundsFrom, portfelsFrom} from "../../utils/StringUtils";

const Portfels = () => {
  const {products} = useContext(DataContext);
  return (
    <Products data={portfelsFrom(products)} type={'portfel'}/>
  );
};

const Funds = () => {
  const {products} = useContext(DataContext);

  return (
    <Products data={fundsFrom(products)} type={'fund'}/>
  );
};

const START_TAB = 0;

const Dashboard = () => {
  const [activeTabIndex, setActiveTabIndex] = useState(START_TAB);

  const tabs = useMemo(() => [{
    name: 'Инвест портфели',
    content: <Portfels/>,
  },{
    name: 'Наш фонд',
    content: <Funds/>,
  },{
    name: 'Мои инвестиции',
    content: <Investitions/>,
  },{
    name: 'Депозит',
    content: <Deposit/>,
  },{
    name: 'Мой профиль',
    content: <Profile/>,
  }], []);

  const tabsContents = useMemo(() => tabs.map(item => item.content), [tabs]);

  const content = <ElectByIndex
    className={s.content}
    index={activeTabIndex}
    children={tabsContents}
  />;

  return (
    <Wrapper className={s.container}>
      <Menu
        tabsInfo={tabs}
        activeTabIndex={activeTabIndex}
        setActiveTabIndex={setActiveTabIndex}
      />
      <Wrapper className={s.main}>
        <Bar goToDeposit={() => setActiveTabIndex(3)}/>
        <Wrapper className={s.contentWrapper}>
          {content}
        </Wrapper>
      </Wrapper>
    </Wrapper>
  );
};

export default Dashboard;
