import s from "./s.module.scss";
import Image from "react-bootstrap/Image";
import cn from "classnames";
import {Text} from "informed";
import React from "react";

const Input = ({iconPath, placeholder, field, inputClassName, ...props}) => (
  <div className={s.input}>
    {iconPath && <div className={s.icon}><Image src={iconPath}/></div>}
    <div className={cn(s.inputElementContainer, !iconPath && 'px-3')}>
      <Text className={cn(s.element, inputClassName)} placeholder={placeholder} field={field} required {...props}/>
    </div>
  </div>
);

export default Input;
