import React, {useState} from 'react';
import s from './s.module.scss';
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Image from "react-bootstrap/Image";
import upIconPath from './up.png';
import Button from '../../../../common/core/Button';
import CustomModal from "./CustomModal";
import Wrapper from "../../../../common/core/Wrapper";
import {ProductType} from "../../../../contexts/DataContext";

const Products = ({data, type}) => {
  const [modalItem, setModalItemState] = useState({});

  const [isModalOpen, setModalOpen] = useState(false);
  const setModalItem = (item) => {
    setModalItemState(item);
    setModalOpen(true);
  };
  const modal = <CustomModal item={modalItem} open={isModalOpen} setModalOpen={setModalOpen}/>;

  const items = data.map((item) => (
    <Item
      key={item.id}
      item={item}
      onButtonClick={() => setModalItem(item)}
    />
  ));

  // const networkContext = useContext(NetworkContext);
  // todo extract commissions and apply on backend
  const commission = type === 'portfel' ? 5 : 30;

  return (
    <Wrapper className={s.container}>
      {modal}
      <Row className={s.itemsContainer}>
        {items}
        <Col xs={12} className={s.warning}>
          Размер комиссии составляет {commission}% от прибыли.
        </Col>
      </Row>
    </Wrapper>
  );
};

const Item = ({item, onButtonClick}) => {
  const {id, name, icon, text, gainPerYearPercents, typeId, minPrice=0, comment='', justifyText} = item;
  const isIconExist = Boolean(icon);
  return (
    <Col xs={12} md={6} lg={4} xl={3} className={s.itemContainer}>
      <div className={s.item}>
        <div className={s.main}>
          <div className={s.header}>
            <div className={s.headerText}>
              {isIconExist && <div className={s.headerIconContainer}>
                <Image src={icon} className={s.headerIconImage}/>
              </div>}
              <div className={s.title}>{name}</div>
            </div>
            <div
              className={s.percents}
              style={{paddingLeft: isIconExist ? 45 : 0}}
            >
              <Image className={s.upIcon} src={upIconPath}/>
              <span className={s.text}>+ {gainPerYearPercents}% ЗА ГОД</span>
            </div>
          </div>
          <div className={s.about} style={justifyText ? {textAlign: 'justify'} : {}}>
            {text.split('\\n').join('\n')}
          </div>
        </div>
        <div className={s.bottom}>
          {minPrice > 0 && <div className={s.minimumPrice}>
            {`мин. цена $${minPrice}`}
          </div>}
          {Boolean(comment) && <div className={s.comment}>{comment}</div>}
          <Button
            containerClassName={s.buttonContainer}
            className={s.button}
            borderColor={'#42287D'}
            color={'#42287D'}
            title={typeId === ProductType.PORTFEL ? 'КУПИТЬ ПОРТФЕЛЬ' : 'ИНВЕСТИРОВАТЬ'}
            onClick={onButtonClick}
          />
        </div>
      </div>
    </Col>
  );

};

export default Products;
