import TouchableOpacity from "../../../../../common/core/TouchableOpacity";
import cn from "classnames";
import s from "../styles/s.module.scss";
import React from "react";

const Pagination = ({canPrevious, canNext, setPage, page, pages}) => {
  const prev = () => canPrevious && setPage(page => page-1);
  const next = () => canNext && setPage(page => page+1);

  pages = pages === Infinity ? 0 : pages;

  const pageNumbers = Array.from({length: pages}, (_, i) => i);

  const PageItem = ({active, onClick, num}) => (
    <TouchableOpacity onClick={onClick} className={cn(s.pageItem, active && s.active)}>
      {num+1}
    </TouchableOpacity>
  );

  const pageItems = pageNumbers.map(num => (
    <PageItem key={num} active={num===page} num={num} onClick={() => setPage(num)}/>
  ));

  return (
    <div className={s.pagination}>
      <TouchableOpacity onClick={prev} className={s.left}><i className={"fas fa-chevron-left"}/></TouchableOpacity>

      <div className={s.center}>
        {pageItems}
      </div>

      <TouchableOpacity onClick={next} className={s.right}><i className={"fas fa-chevron-right"}/></TouchableOpacity>
    </div>
  );
};

export default Pagination;
