import useMouseDown from "../../hooks/useMouseDown";
import styles from './style.module.css';
import cn from 'classnames';
import React, {useRef} from "react";

const TouchableOpacity = React.forwardRef(({inline, children, className, disabled, ...otherProps}, ref) => {
  ref = ref ? ref : useRef(null);
  const pressing = useMouseDown(ref);

  return (
    inline ?
      <span
        className={cn(styles.element, !disabled && pressing && styles.pressing, className)}
        ref={ref}
        {...otherProps}
      >
        {children}
      </span>
    :
      <div
        className={cn(styles.element, !disabled && pressing && styles.pressing, className)}
        ref={ref}
        {...otherProps}
      >
        {children}
      </div>
  );
});

export default TouchableOpacity;