import Col from "react-bootstrap/Col";
import {Container, Row} from "react-bootstrap";
import Image from "react-bootstrap/Image";
import React from "react";

// todo abstract

const IMAGES_COUNT = 6;

function Partners() {
  return (
    <Container className="justify-content-center">
      <Row className="justify-content-xs-start justify-content-sm-between">
        {[...Array(IMAGES_COUNT)].map((_, i) => (
          <ImageContainer>
            <Image src={require(`./images/${i+1}.png`)} style={{width: '100%'}}/>
          </ImageContainer>
        ))}
      </Row>
    </Container>
  );
}

function ImageContainer(props) {
  return (
    <Col
      xs={6} sm={12/IMAGES_COUNT}
      className="justify-content-center align-items-center p-2"
      {...props}
    />
  );
}

export default Partners;
