import TouchableOpacity from "../TouchableOpacity";
import React from "react";
import styles from './style.module.css';
import cn from 'classnames';
import {BarLoader} from "react-spinners";

const Button = ({borderColor, backgroundColor='transparent', color, width, height, className, onClick, containerClassName, title, disabled, waiting=false, waitingTitle, waitingColor}) => (
  <TouchableOpacity
    onClick={disabled ? () => {} : onClick}
    className={cn(containerClassName)}
    disabled={disabled}
  >
    <button
      className={cn(styles.button, className)}
      style={{
        borderColor: disabled ? 'rgba(255,255,255,0.2)' : borderColor,
        color: disabled ? 'rgba(255,255,255,0.2)' : color,
        backgroundColor: backgroundColor,
        width,
        height
      }}
      disabled={disabled}
    >
      {waiting ? (
        <LoadingButtonTitle
          text={waitingTitle || title}
          color={waitingColor}
        />) : title}
    </button>
  </TouchableOpacity>
);

export const LoadingButtonTitle = ({text, color='rgba(26,192,234,0.2)'}) => (
  <>
    <div className={styles.loaderContainer}>
      <BarLoader color={color} width={100} height={100} heightUnit={'%'} widthUnit={'%'}/>
    </div>
    <div className={styles.loaderText}>{'ПОДОЖДИТЕ'}</div>
  </>
);

export default Button;
