import React, {useEffect, useMemo, useState} from 'react';
import s from './styles/s.module.scss';
import cn from 'classnames';
import 'react-table/react-table.css';
import moment from "moment";
import TouchableOpacity from "../../../../common/core/TouchableOpacity";
import formatMoney from "../../../../utils/formatMoney";
import './styles/react-table-styling.scss';
import Wrapper from "../../../../common/core/Wrapper";
import ElectByIndex from "../../../../common/core/ElectByIndex";
import InvestmentsTable from "./parts/InvestmentsTable";
import OperationsTable from "./parts/OperationsTable";

moment.locale('ru');

export const ROW_HEIGHT = 55;

const getPageSize = () => {
  const availableHeight = document.documentElement.clientHeight - 250;
  return Math.floor((availableHeight > 0 ? availableHeight : 0)/ROW_HEIGHT);
};

const Investitions = () => {
  const [pageSize, setPageSize] = useState(getPageSize());
  const onResize = () => setPageSize(getPageSize());

  useEffect(() => {
    window.addEventListener('resize', onResize);
    return () => window.removeEventListener('resize', onResize);
  }, []);

  const [tabIndex, setTabIndex] = useState(0);
  const [sum, setSum] = useState(0);

  const tabs = useMemo(() => [{
    name: 'Инвестиции',
    content: (
      <InvestmentsTable
        pageSize={pageSize}
        setSum={setSum}
      />
    ),
  },{
    name: 'История операций',
    content: (
      <OperationsTable
        pageSize={pageSize}
      />
    ),
  }].map((item, index) => ({
    ...item,
    onClick: () => setTabIndex(index)})
  ), [pageSize]);

  return (
    <Wrapper className={s.container}>
      <div className={s.tablesHat}>
        {tabs.map(({name, onClick}, index) => (
          <TouchableOpacity
            className={cn(
              s.tablesHatTab,
              index === tabIndex && s.tablesHatTabActive)
            }
            onClick={onClick}
          >
            {name}
          </TouchableOpacity>
        ))}
        <div className={s.sum}>
          Cумма всех активов:&ensp;
          <span className={s.num}>{formatMoney(sum)}</span>
        </div>
      </div>
      <ElectByIndex
        index={tabIndex}
      >
        {tabs.map(({content}) => content)}
      </ElectByIndex>
    </Wrapper>
  );
};

export default Investitions;
