import React from "react";
import {Col, Container} from "react-bootstrap";
import styles from './styles.module.scss';
import Image from "react-bootstrap/Image";
import Row from "react-bootstrap/Row";

const items = [{
  title: 'РЕПУТАЦИЯ',
  icon: require('./images/reputation.png'),
  text: 'Компания дорожит именем и репутацией, которые заслужила за годы упорной работы.',
},{
  title: 'НАДЁЖНОСТЬ',
  icon: require('./images/reliability.png'),
  text: 'В основе нашей работы прозрачные условия сотрудничества, грамотное управление рисками и активный подход к управлению инвестициями.',
},{
  title: 'УДОБСТВО',
  icon: require('./images/convenience.png'),
  text: 'Инвестировать деньги стало гораздо проще. Стратегии управления активами нацелены на результат.',
},{
  title: 'КЛИЕНТООРИЕНТИРОВАННОСТЬ',
  icon: require('./images/customers.png'),
  text: 'Подбор финансовых решений осуществляется исходя из запросов и потребностей клиента.',
},{
  title: 'ПАРТНЕРСТВО',
  icon: require('./images/partnership.png'),
  text: 'Мы признаем только взаимовыгодное сотрудничество. Ценим эффективность командной работы.'
},{
  title: 'РАЗВИТИЕ',
  icon: require('./images/development.png'),
  text: 'Совершенствование бизнес-процессов позволяет повышать качество предоставляемых услуг.',
}];

function Item({title, icon, text}) {
  return (
    <Col xs={12} sm={6} lg={4} className={styles.item}>
      <Image src={icon} className={styles.icon}/>
      <div className={styles.title}>{title}</div>
      <div className={styles.text}>{text}</div>
    </Col>
  );
}

function WhyUs() {
  return (
    <div className={styles.container}>
      <Container className="py-3">
        <Row>
          {items.map(Item)}
        </Row>
      </Container>
    </div>
  );
}

export default WhyUs;
