import s from "../styles/s.module.scss";
import cn from "classnames";
import formatMoney from "../../../../../utils/formatMoney";
import Image from "react-bootstrap/Image";
import upIconPath from "../images/up.png";
import downIconPath from "../images/down.png";
import React from "react";

const GainCell = ({value}) => {
  const ifZero = value === 0;
  const positive = value > 0;

  return (
    <div className={s.gain}>
      <span className={cn(!ifZero && (positive ? s.positive : s.negative))}>{formatMoney(value)}</span>
      {!ifZero && <Image
        height={18}
        className={positive ? s.iconPositive : s.iconNegative}
        src={positive ? upIconPath : downIconPath}/>}
    </div>
  );
};

export default GainCell;
