import Pagination from "./Pagination";
import ReactTable from "react-table";
import React, {useContext, useEffect, useState} from "react";
import s from "../styles/s.module.scss";
import formatMoney from "../../../../../utils/formatMoney";
import GainCell from "./GainCell";
import ActionCell from "./ActionCell";
import Modal from "../../../../../common/core/Modal";
import moment from "moment";
import TableHeader from "./TableHeader";
import DataContext, {InvestmentStatus} from "../../../../../contexts/DataContext";

export const createInvestmentsData = ({user, products}) => {
  const {investments=[]} = user.data;
  const findProductById = (id) => products.data.find((item) => item.id === id);

  const data = investments
    .filter(({statusId}) => statusId === InvestmentStatus.ACTIVE_INVESTMENT || statusId === InvestmentStatus.UNCONFIRMED_SALE)
    .map((investment) => {
      const {initSum, currentSum, months, unlimited, productId, createdAt} = investment;
      const {name: productName, typeId: productTypeId, gainHistory = []} = findProductById(productId);

      const todayEntry = [...gainHistory].reverse().find(({date}) => {
        const endDate = moment(date).add(24, 'h');
        const currentDate = moment();
        return (
          currentDate.isSameOrAfter(date) &&
          currentDate.isSameOrBefore(endDate)
        );
      });

      const todayGain = todayEntry ? todayEntry.gain : 0;

      const profitMoney = currentSum - initSum;
      const profitPercents = profitMoney / initSum * 100;

      const expirationDate = moment(createdAt).add(months, 'months');

      return {
        productName,
        initSum,
        currentSum,
        todayGain,
        expirationDate: unlimited ? 'БЕССРОЧНЫЙ' : expirationDate.format('DD.MM.YYYY'),
        profitMoney,
        profitPercents,
        actionData: {
          investment,
          productTypeId,
          canSell: unlimited ? true : moment().isAfter(expirationDate),
        },
      };
    });

  const allCurrentSum = data.reduce((memo, {currentSum}) => memo + currentSum, 0);

  return {data, sum: allCurrentSum};
};

const investmentColumns = [{
  Header: <TableHeader name={'НАЗВАНИЕ'}/>,
  Cell: ({value}) => <span className={s.name}>{value}</span>,
  accessor: 'productName',
  width: 180,
}, {
  Header: <TableHeader name={'СУММА'}/>,
  Cell: ({value}) => formatMoney(value),
  accessor: 'initSum',
  width: 100,
}, {
  Header: <TableHeader name={'СРОК'}/>,
  accessor: 'expirationDate',
  width: 150,
}, {
  Header: <TableHeader name={'ЗА ДЕНЬ(%)'}/>,
  Cell: ({value}) => `${value}%`.replace('.', ','),
  accessor: 'todayGain',
  width: 110,
}, {
  Header: <TableHeader name={'ПРИБЫЛЬ(%)'}/>,
  Cell: ({value}) => `${value}%`.replace('.', ','),
  accessor: 'profitPercents',
  width: 110,
}, {
  Header: <TableHeader name={'ПРИБЫЛЬ($)'}/>,
  Cell: ({value}) => <GainCell value={value}/>,
  accessor: 'profitMoney',
  width: 110,
}, {
  Header: <TableHeader name={'СТОИМОСТЬ'}/>,
  Cell: ({value}) => formatMoney(value),
  accessor: 'currentSum',
  width: 110,
}];

const InvestmentsTable = ({pageSize, setSum}) => {
  const [page, setPage] = useState(0);
  const [isModalOpen, setModalOpen] = useState(false);

  const {
    user,
    products,
  } = useContext(DataContext);

  const {data, sum} = createInvestmentsData({user, products});
  useEffect(() => setSum(sum), [setSum, sum]);

  const columns = investmentColumns.concat({
    Header: null,
    Cell: ({value}) => <ActionCell {...value} setModalOpen={setModalOpen}/>,
    accessor: 'actionData',
    width: 190,
  });

  return (
    <>
      <ReactTable
        className={'table'}
        columns={columns}
        data={data}
        sortable={false}
        resizable={false}
        pageSize={pageSize}
        minRows={0}
        page={page}
        showPageSizeOptions={false}
        PaginationComponent={(props) => (
          props.pages > 1 && <Pagination setPage={setPage} {...props}/>
        )}
        NoDataComponent={() => null}
      />
      <CustomModal
        isOpen={isModalOpen}
        setModalOpen={setModalOpen}
      />
    </>
  );
};

const CustomModal = ({isOpen, setModalOpen}) => {
  const {email} = useContext(DataContext).user.data;
  return (
    <Modal
      isOpen={isOpen}
      onClose={() => {
        setModalOpen(false);
      }}
    >
      <div className={s.confirm}>
        Пожалуйста, подтвердите операцию.
        Ссылка для подтверждения отправлена на почту:&ensp;<span className={s.email}>{email}</span>
      </div>
    </Modal>
  );
};

export default InvestmentsTable;
