import React from "react";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import styles from './styles.module.scss';
import Button from "../../../../../../common/core/Button";
import colors from "../../../../../../common/colors";
import { useHistory } from 'react-router-dom';
import cn from 'classnames';

function Covers() {
  return (
    <Row>
      <Item
        title="ИНВЕСТИЦИОННЫЙ ПОРТФЕЛЬ"
        text="У инвестиционных портфелей две основные задачи: обеспечение максимальной стабильной прибыли и минимизация возможности потери активов. Наши специалисты распределяют капитал по разным продуктам и инструментам, надежно защищая средства и обеспечивая доход выше среднего по рынку."
        className={styles.portfel}
      />
      <Item
        title="НАШ ФОНД"
        text="Инвестиционные фонды – это простой и доступный способ вложения денег с помощью профессиональных управляющих в ценные бумаги с возможностью получения большего дохода, чем по вкладам. Вложения в инвестиционные фонды удобны для тех, кто не готов самостоятельно принимать инвестиционные решения из-за недостатка времени или опыта."
        className={styles.fund}
      />
    </Row>
  );
}

function Item({title, text, link, className}) {
  const history = useHistory();

  return (
    <Col xs={12} lg={6} className={cn(styles.item, className)}>
      <div>
        <div className={styles.title}>{title}</div>
        <div className={styles.text}>{text}</div>
      </div>
      <Button
        title="ПОДРОБНЕЕ"
        color={colors.blue}
        onClick={() => history.push(link)}
        containerClassName={styles.button}
      />
    </Col>
  );
}

export default Covers;
