import React, {useContext, useState} from "react";
import Row from "react-bootstrap/Row";
import s from "./s.module.scss";
import Col from "react-bootstrap/Col";
import {Form} from "informed";
import loginIconPath from "./images/icon_login.png";
import passwordIconPath from "./images/icon_password.png";
import Checkbox from "../../../../common/core/Checkbox";
import TouchableOpacity from "../../../../common/core/TouchableOpacity";
import cn from "classnames";
import CustomButton, {LoadingButtonTitle} from "../../../../common/core/Button";
import ForgotModal from "./ForgotModal";
import Input from "./Input";
import {loginUser} from "../../../../api/requests";
import NetworkContext from "../../../../contexts/NetworkContext";

export const Login = () => {
  const [isFailed, setFailed] = useState(false);
  const [failText, setFailText] = useState('');
  const [waiting, setWaiting] = useState(false);
  const [isModalOpen, setModalOpen] = useState(false);

  const {onLogin} = useContext(NetworkContext);

  const login = async ({email, password, remember}) => {
    setWaiting(true);
    setFailed(false);

    const {success, error, data} = await loginUser({email, password});
    if (success) {
      onLogin(data.token, remember);
    } else if (error === 'user-email-not-found') {
      setFailText('Пользователь не найден');
      setFailed(true);
    } else if (error === 'wrong-password') {
      setFailText('Неверный пароль');
      setFailed(true);
    } else {
      setFailText('Произошла ошибка, попробуйте еще раз');
      setFailed(true);
    }

    setWaiting(false);
  };

  const [formState, setFormState] = useState({values: {}});

  return (
    <Row className={s.row}>
      <Col xs={10} style={{maxWidth: 400}} className={'d-flex justify-content-center'}>
        <div className={s.container}>
          {isModalOpen && <ForgotModal initEmail={formState.values.email} isOpen={isModalOpen} onClose={() => setModalOpen(false)}/>}
          <Form
            onSubmit={login}
            initialValues={{remember: true}}
            onChange={setFormState}
          >
            <Input
              placeholder={'Email'}
              field={'email'}
              iconPath={loginIconPath}
              type={'email'}
            />
            <Input
              field={'password'}
              type={'password'}
              placeholder={'Пароль'}
              iconPath={passwordIconPath}
              inputClassName={s.password}
            />
            <Row className={'d-flex justify-content-center align-items-center'}>
              <Col className={s.remember} xs={6}>
                <Checkbox field={'remember'} className={s.checkbox}/>
                Запомнить меня
              </Col>
              <Col className={s.forgot} xs={6}>
                <TouchableOpacity onClick={() => setModalOpen(true)}>Забыли пароль?</TouchableOpacity>
              </Col>
              <div className={cn(s.fail, isFailed && s.showing)}>
                {isFailed && failText}
              </div>
            </Row>
            <CustomButton
              title={waiting ? <LoadingButtonTitle text={'ВОЙТИ'}/> : 'ВОЙТИ'}
              color={'#1ac0ea'}
              borderColor={'#1ac0ea'}
              className={s.button}
            />
          </Form>
        </div>
      </Col>
    </Row>
  );
};

export default Login;
