import React, {useContext, useEffect, useState} from 'react';
import s from "./s.module.scss";
import Image from "react-bootstrap/Image";
import TouchableOpacity from "../../../../common/core/TouchableOpacity";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import icon1Path from './images/icon1.png';
import icon2Path from './images/icon2.png';
import icon3Path from './images/icon3.png';
import icon4Path from './images/icon4.png';
import Modal from "../../../../common/core/Modal";
import {ItemText, SimpleText, Title} from "../About";
import DataContext from "../../../../contexts/DataContext";
import {fundsFrom, portfelsFrom} from "../../../../utils/StringUtils";

const SubTitle = ({children}) => (
  <Title style={{fontSize: '1.4rem', justifyContent: 'flex-start', textAlign: 'left', paddingBottom: 0}}>{children}</Title>
);

const Services = ({openModal}) => {
  const {products} = useContext(DataContext);
  const productsPortfels = portfelsFrom(products);
  const productsFunds = fundsFrom(products);

  const [modal, setModal] = useState(null);
  useEffect(() => {
    if (openModal.name === 'portfels') setModal(portfelModal);
    else if (openModal.name === 'funds') setModal(fundModal);
  }, [openModal]);

  const portfelModal = <Modal
    isOpen={true}
    onClose={() => setModal(null)}
    className={s.modal}
    contentStyle={{
      maxWidth: 'unset',
      width: '80%',
      height: '80%',
    }}
    useDefaultStyling={false}
  >
    <div className={s.content}>
      <Title>Инвестиционный портфель – максимальная доходность и диверсификация рисков</Title>
      <SimpleText>У инвестиционных портфелей две основные задачи: обеспечение максимальной стабильной прибыли и минимизация возможности потери активов. Наши специалисты распределяют капитал по разным продуктам и инструментам, надежно защищая средства и обеспечивая доход выше среднего по рынку.</SimpleText>
      <Title>Преимущества</Title>
      <ItemText>Создание условий для сохранности средств. Когда одни инструменты и продукты, входящие в портфель, приносят убытки, то прибыль от других их полностью компенсирует. Этот принцип работает в любых рыночных условиях.</ItemText>
      <ItemText>Обеспечение высокой и стабильной прибыли. Специалисты вкладывают деньги в надежные и высокодоходные инструменты.</ItemText>
      <Title>Как это работает?</Title>
      <SimpleText>При формировании портфелей используются главные принципы распределения активов. Прежде всего, большая доля средств вкладывается в один или несколько основных активов, отличающихся стабильностью и высокой ликвидностью. Еще одна часть работает в активах компаний, зарекомендовавших себя как наиболее надежные участники рынка, это позволяет защитить капитал от инфляции. И третья, минимальная часть портфеля, инвестируется в высокодоходные, но рискованные активы. Они могут принести высокую прибыль, но в случае неудачи, потеря не отразится на общем балансе.</SimpleText>
      <SimpleText>Мы собрали для вас портфели наиболее прибыльных отраслей. Распределение активов осуществляется с учетом всех факторов, которые могут повлиять на ситуацию на рынке. Эксперты используют надежные и проверенные схемы, чтобы Ваши инвестиции приносили максимально высокую прибыль.</SimpleText>
      <Title>Выберите свой Инвестиционный портфель:</Title>
      {productsPortfels.map(({name, about}) => (
        <React.Fragment key={name}>
          <SubTitle className={s.productTitle}>{`Портфель ${name}`}</SubTitle>
          <SimpleText>{about}</SimpleText>
        </React.Fragment>
      ))}
    </div>
  </Modal>;

  const fundModal = <Modal
    isOpen={true}
    onClose={() => setModal(null)}
    className={s.modal}
    contentStyle={{
      maxWidth: 'unset',
      width: '80%',
      height: '80%',
    }}
    useDefaultStyling={false}
  >
    <div className={s.content}>
      <Title>Наш Фонд</Title>
      <SimpleText>Инвестиционные фонды – это простой и доступный способ вложения денег с помощью профессиональных управляющих в ценные бумаги с возможностью получения большего дохода, чем по вкладам. Вложения в инвестиционные фонды удобны для тех, кто не готов самостоятельно принимать инвестиционные решения из-за недостатка времени или опыта.</SimpleText>

      <Title>Как это работает?</Title>
      <SubTitle>1. ВЫ ВЫБИРАЕТЕ ФОНД</SubTitle>
      <SimpleText>Вкладываете в него деньги , передавая деньги в профессиональное управление с целью получения дохода.</SimpleText>
      <SubTitle>2. СРЕДСТВА ФОНДА ИНВЕСТИРУЮТСЯ</SubTitle>
      <SimpleText>В инструменты финансового рынка. Управляющая команда заботится о сохранении и приумножении активов фонда.</SimpleText>
      <SubTitle>3. ВЫ ПОЛУЧАЕТЕ ДОХОД</SubTitle>
      <SimpleText>Доход образуется в случае роста капитализаций фонда. Однако такой рост не гарантирован. Капитализация может снижаться.</SimpleText>

      <Title>Преимущества</Title>
      <SubTitle>1. ДОСТУПНЫЕ И УДОБНЫЕ</SubTitle>
      <SimpleText>Минимальная сумма вложений от 50000 рублей (в Личном кабинете). Пополнение открытых фондов в любой момент</SimpleText>
      <SubTitle>2. ВЫСОКОЛИКВИДНЫЕ</SubTitle>
      <SimpleText>Вы можете вывести инвестированные деньги в любой рабочий день.</SimpleText>
      <SubTitle>3. УПРАВЛЯЮТСЯ ПРОФЕССИОНАЛАМИ</SubTitle>
      <SimpleText>В “НАШ ФОНД” Управление Активами одна из лучших команд управляющих Инвестиционными фондами.</SimpleText>
      <Title>Выберите ИФ по уровню риска/доходности:</Title>
      {productsFunds.map(({name, about}) => (
        <React.Fragment key={name}>
          <SubTitle className={s.productTitle}>{`"НАШ ФОНД" ${name}`}</SubTitle>
          <SimpleText>{about}</SimpleText>
        </React.Fragment>
      ))}
    </div>
  </Modal>;

  return (
    <>
      <Row className={s.container}>
        {modal}
        <ServiceUnit
          title={'ИНВЕСТИЦИОННЫЙ ПОРТФЕЛЬ'}
          text={'У инвестиционных портфелей две основные задачи: обеспечение максимальной стабильной прибыли и минимизация возможности потери активов. Наши специалисты распределяют капитал по разным продуктам и инструментам, надежно защищая средства и обеспечивая доход выше среднего по рынку.'}
          buttonText={'ПОДРОБНЕЕ'}
          onClick={() => setModal(portfelModal)}
          iconPath={icon4Path}
        />
        <ServiceUnit
          title={'НАШ ФОНД'}
          text={'Инвестиционные фонды – это простой и доступный способ вложения денег с помощью профессиональных управляющих в ценные бумаги с возможностью получения большего дохода, чем по вкладам. Вложения в инвестиционные фонды удобны для тех, кто не готов самостоятельно принимать инвестиционные решения из-за недостатка времени или опыта.'}
          buttonText={'ПОДРОБНЕЕ'}
          onClick={() => setModal(fundModal)}
          iconPath={icon2Path}
        />
        <ServiceUnit
          title={'ПАМП СЧЕТА'}
          text={''}
          buttonText={'COMING SOON'}
          iconPath={icon3Path}
        />
        <ServiceUnit
          title={'ДОВЕРИТЕЛЬНОЕ УПРАВЛЕНИЕ'}
          text={''}
          buttonText={'COMING SOON'}
          iconPath={icon1Path}
        />
      </Row>
    </>
  );
};

const ServiceUnit = ({title, text, iconPath, buttonText, onClick}) => (
  <Col md={5}>
    <div className={s.service}>
      <div className={s.header}>
        <div className={s.icon}>
          <Image src={iconPath} height={30}/>
        </div>
        <div className={s.title}>{title}</div>
      </div>
      <div className={s.info}>
        {text && <div className={s.text}>{text}</div>}
        <TouchableOpacity className={s.button} onClick={onClick}>
          {buttonText}
        </TouchableOpacity>
      </div>
    </div>
  </Col>
);

export default Services;
