import {Slider, Switch, withStyles} from "@material-ui/core";
import React, {useContext, useState} from "react";
import s from "./s.module.scss";
import Button from "../../../../common/core/Button";
import Modal from "../../../../common/core/Modal";
import {Box} from "../Deposit";
import DataContext, {ProductType} from "../../../../contexts/DataContext";
import {requestPurchase} from "../../../../api/requests";
import useWaiting from "../../../../common/hooks/useWaiting";

const SLIDER_HEIGHT = 6;
const THUMB_SIZE = 20;
const THUMB_SIZE_DISABLED = 16;

const CustomSlider = withStyles({
  root: {
    '&$disabled $thumb': {
      width: THUMB_SIZE_DISABLED,
      height: THUMB_SIZE_DISABLED,
      marginTop: -THUMB_SIZE_DISABLED/2 + SLIDER_HEIGHT/2,
      marginLeft: -THUMB_SIZE_DISABLED/2,
      color: '#aaaaaa',
    },
    '&$disabled $rail': {
      color: '#aaaaaa',
    },
    '&$disabled $track': {
      color: 'rgba(170,170,170,0.67)',
    }
  },
  rail: {
    height: SLIDER_HEIGHT,
    color: 'rgba(43,192,232,0.71)',
  },
  track: {
    height: SLIDER_HEIGHT,
    color: '#2BC0E8',
  },
  thumb: {
    width: THUMB_SIZE,
    height: THUMB_SIZE,
    marginTop: -THUMB_SIZE/2+SLIDER_HEIGHT/2,
    marginLeft: -THUMB_SIZE/2,
    color: '#2BC0E8',
  },
  disabled: {},
})(Slider);

const CustomSwitch = withStyles({
  root: {

  },
  switchBase: {
    '&$checked': {
      color: '#2BC0E8',
    },
    '&$checked + $track': {
      backgroundColor: 'rgba(43,192,232,0.71)',
    },
  },
  checked: {},
  track: {},
})(Switch);

const CustomModal = ({item, open, setModalOpen}) => {
  const {id, name, typeId, minPrice=0} = item;

  const {user} = useContext(DataContext);
  const {email, balance, transactionBalance, verified} = user.data;
  const allBalance = balance + transactionBalance;

  const [unlimited, setUnlimited] = useState(false);
  const [months, setMonths] = useState(12);
  const [success, setSuccess] = useState(false);

  const [sum, setSum] = useState(minPrice);
  const insufficient = allBalance < Number(sum);
  const lessThenMin = Number(sum) < minPrice;
  const disabled = !verified || !Boolean(sum) || insufficient || lessThenMin;

  const buttonTittleFromType = typeId === ProductType.PORTFEL ? 'КУПИТЬ ПОРТФЕЛЬ' : 'ИНВЕСТИРОВАТЬ';
  const buttonTitleFromInsufficient = insufficient ? 'НЕДОСТАТОЧНО СРЕДСТВ' : buttonTittleFromType;
  const buttonTitle = verified ? buttonTitleFromInsufficient : 'НЕОБХОДИМА ВЕРИФИКАЦИЯ';

  const onClose = () => {
    setModalOpen(false);
    // TODO !!??
    setSum('');
    setUnlimited(false);
    setMonths(12);
    setSuccess(false);
  };

  const [waiting, requestPurchaseWithWaiting] = useWaiting(requestPurchase);

  const onButtonClick = async () => {
    const purchase = {
      productId: id,
      sum: sum,
      unlimited,
      months: unlimited ? null : months
    };

    await requestPurchaseWithWaiting(purchase);

    setSuccess(true);
  };

  const title = `${name} ${unlimited ? `(${typeId === ProductType.PORTFEL ? 'БЕССРОЧНЫЙ' : 'БЕССРОЧНО'})` : `НА ${months} МЕС`}`;

  return (
    <Modal isOpen={open} onClose={onClose} className={s.modal}>
      {success ?
        <>
          <div className={s.confirm}>
            Пожалуйста, подтвердите операцию.
            Ссылка для подтверждения отправлена на почту:&ensp;<span className={s.email}>{email}</span>
          </div>
        </>
        :
        <>
          <div className={s.title}>
            {title}
          </div>
          <div className={s.slider}>
            <CustomSlider
              min={1}
              max={36}
              value={months}
              disabled={unlimited}
              onChange={(_, value) => setMonths(value)}
            />
          </div>
          <div className={s.sliderInfo}>
            <span>1 МЕС</span>
            <span>36 МЕС</span>
          </div>
          <div className={s.switch}>
            <CustomSwitch
              color="primary"
              checked={unlimited}
              onChange={(_, checked) => setUnlimited(checked)}
            />
            {typeId === ProductType.PORTFEL ? 'Бессрочный' : 'Бессрочно'}
          </div>
          <div className={s.sum}>
            <div>Введите сумму:</div>
            <Box
              style={{width: 140, height: 40, marginLeft: '1rem', borderRadius: '0.2rem', overflow: 'hidden', backgroundColor: '#FAFAFA'}}
              placeholder={'USD'}
              value={sum}
              onChange={(e) => setSum(e.target.value)}
            />
          </div>
          <Button
            className={s.button}
            containerClassName={s.buttonContainer}
            title={buttonTitle}
            onClick={onButtonClick}
            disabled={disabled}
            waiting={waiting}
          />
        </>}
    </Modal>
  );
};

export default CustomModal;
