import React, {useContext, useState} from 'react';
import s from './s.module.scss';
import cn from 'classnames';
import Wrapper from "../../../../common/core/Wrapper";
import TouchableOpacity from "../../../../common/core/TouchableOpacity";
import Modal from "../../../../common/core/Modal";
import ReactTable from "react-table";
import TableHeader from "../Investitions/parts/TableHeader";
import moment from "moment";
import {Form, Text} from 'informed';
import CustomButton from "../../../../common/core/Button";
import formatMoney, {usdToRub, formatMoneyRub, cc_format} from "../../../../utils/formatMoney";
import DataContext from "../../../../contexts/DataContext";
import {getWithdrawEmail} from "../../../../api/requests";

const Deposit = () => {
  const [depositAmount, setDepositAmount] = useState('');
  const [modalWithdrawIsOpen, setModalWithdrawOpen] = useState(false);
  const {user} = useContext(DataContext);
  const {email} = user.data;

  const [depositModal, setDepositModal] = useState(null);

  const openDepositModal = () => {
    setDepositModal(
      <ModalDeposit
        initAmount={depositAmount}
        isOpen={true}
        onClose={() => setDepositModal(null)}
      />
    );
  };

  const openTransactionModal = (transaction) => {
    setDepositModal(
      <ModalDeposit
        isOpen={true}
        initAmount={depositAmount}
        initTransaction={transaction}
        onClose={() => setDepositModal(null)}
      />
    );
  };

  const withdraw = async () => {
    await getWithdrawEmail();
    setModalWithdrawOpen(true);
    // const withdraw = firebase.functions().httpsCallable('sendWithdrawEmail');
    // await withdraw();
  };

  return (
    <Wrapper className={s.container}>
      <div className={s.content}>
        <div className={s.deposit}>
          <Box placeholder={'USD:'} onChange={(e) => setDepositAmount(e.target.value)}/>
          <Button title={'ПОПОЛНИТЬ'} onClick={openDepositModal}/>
        </div>
        <div className={s.withdraw}>
          <Box placeholder={'USD:'}/>
          <Button title={'ВЫВЕСТИ'} onClick={withdraw}/>
        </div>
        <Table openTransactionModal={openTransactionModal}/>
      </div>
      {depositModal}
      <ModalWithdraw isOpen={modalWithdrawIsOpen} onClose={() => setModalWithdrawOpen(false)} email={email}/>
    </Wrapper>
  );

};

const TransactionStatus = {
  PRELIMINARY: 'preliminary',
  NEW: 'new',
  FROZEN: 'frozen',
  COMPLETED: 'completed',
  DELETED: 'deleted',
  ERROR: 'error',
};

const Table = ({openTransactionModal}) => {
  const {transactions=[]} = useContext(DataContext).user.data;

  const columns = [{
    Header: <TableHeader name={'СУММА'}/>,
    Cell: ({value}) => formatMoney(value),
    accessor: 'usdAmount',
    width: 120,
  },{
    Header: <TableHeader name={'ДАТА'}/>,
    Cell: ({value}) => moment(value).format('DD.MM.YYYY'),
    accessor: 'createdAt',
    width: 120,
  },{
    Header: <TableHeader name={'СТАТУС ЗАЯВКИ'}/>,
    Cell: ({value, index}) => (
      <Status
        status={value}
        confirmAction={() => openTransactionModal(transactions[index])}
      />
    ),
    accessor: 'status',
  }];

  return (
    <div className={s.tableContainer}>
      <ReactTable
        className={'table'}
        columns={columns}
        data={transactions.sort((a, b) => {
          return moment(a.created).isBefore(moment(b.created)) ? 1 : -1;
        })}
        sortable={false}
        resizable={false}
        minRows={0}
        showPageSizeOptions={false}
        PaginationComponent={() => null}
        NoDataComponent={() => null}
      />
    </div>
  );
};

const Status = ({status, confirmAction}) => {
  let content = null;

  if (status === TransactionStatus.PRELIMINARY) {
    content = (
      <TouchableOpacity
        className={s.tableConfirm}
        onClick={confirmAction}
      >
        Подтвердить оплату
      </TouchableOpacity>
    );
  } else if (status === TransactionStatus.NEW || status === TransactionStatus.FROZEN) {
    content = (
      <div>Обрабатывается</div>
    );
  } else if (status === TransactionStatus.COMPLETED) {
    content = (
      <div>Подтверждена</div>
    );
  } else if (status === TransactionStatus.DELETED || status === TransactionStatus.ERROR) {
    content = (
      <div>Отменена</div>
    );
  }
  return content;
};

const ModalDeposit = ({isOpen, onClose, initTransaction, initAmount}) => {
  const [waiting, setWaiting] = useState(false);
  const [transaction, setTransaction] = useState(initTransaction);
  const [failText, setFailText] = useState('');
  const {usdRate, account1, account2, account3} = useContext(DataContext).settings.data;

  const deposit = async ({usdAmount, fromAcc}) => {
    fromAcc = fromAcc.split(' ').join('');
    setWaiting(true);
    setFailText('');

    // const createTransaction = firebase.functions().httpsCallable('createDepositTransaction');
    // const {data:res} = await createTransaction({usdAmount, fromAcc});

    // const {ok, data, message} = res;
    // if (ok) {
    //   setTransaction(data);
    // } else {
    //   setFailText(message);
    // }
    setWaiting(false);
  };

  const confirm = async () => {
    setWaiting(true);
    // const confirmTransaction = firebase.functions().httpsCallable('userConfirmTransaction');
    // const res = await confirmTransaction({id: transaction.id});
    // const {data} = res;
    // const {ok, message} = data;
    // if (ok) {
    //   onClose();
    // } else {
    //   setFailText(message);
    // }
    setWaiting(false);
  };

  const fail = <div className={cn(s.fail, failText && s.failActive)}>{failText}</div>;

  const transactionInfo = transaction ? (
    <div>
      <div className={s.transactionHeader}>
        Заявка №{transaction.id}
      </div>
      {
        transaction.requisites && (
          <div className={s.transactionRequisites}>
            {transaction.requisites}
          </div>
        )
      }
      <div className={s.transactionSum}>
        Сумма перевода:&nbsp;
        <span style={{fontWeight: 700}}>
          {formatMoneyRub(transaction.rubAmount)}
        </span>
      </div>
      <CustomButton
        title={'НА СТРАНИЦУ ОПЛАТЫ'}
        containerClassName={s.transactionPageButton}
        onClick={() => {
          window.open(transaction.link);
        }}
      />
      <CustomButton
        title={'Я ПЕРЕВЕЛ'}
        onClick={confirm}
        waiting={waiting}
        waitingColor={'rgba(255,255,255,0.2)'}
      />
      {fail}
    </div>
  ) : (
    <Form
      onSubmit={deposit}
      initialValues={{usdAmount: initAmount}}
      autoComplete={'off'}
    >
      {({formState}) => {
        return (<>
          <div className={s.modalHeader}>
            Укажите номер банковской карты:
          </div>
          <Text
            className={s.cardField}
            field={'fromAcc'}
            required
            autoComplete={'off'}
            mask={(value) => {
              return value && cc_format(value);
            }}
          />
          <div className={s.modalHeader}>
            Введите сумму:
          </div>
          <Box
            placeholder={'USD:'}
            field={'usdAmount'}
            className={s.amountField}
            step={0.01}
            required
          />
          <div className={s.modalHeader}>
            Будет списано:&ensp;
            <span style={{fontWeight: 700}}>
              {formatMoneyRub(
                usdToRub(formState.values.usdAmount, usdRate)
              )}
            </span>
          </div>
          <CustomButton
            title={'ПОПОЛНИТЬ'}
            waiting={waiting}
            waitingColor={'rgba(255,255,255,0.2)'}
          />
          {fail}
        </>)
      }}
    </Form>
  );

  // const acc1 = '4890 4945 6444 3588';
  // const acc2 = '5599 0050 3212 5317';

  const bank = (
    <div className={s.bank}>
      <Form
        initialValues={{usdAmount: initAmount}}
        autoComplete={'off'}
      >
        {({formState}) => (
          <>
            <div className={s.bankControl}>
              <Box
                placeholder={'USD:'}
                field={'usdAmount'}
                className={s.amountField}
                step={0.01}
                required
              />
              <CustomButton
                title={'Я ПЕРЕВЕЛ'}
                waiting={waiting}
                waitingColor={'rgba(255,255,255,0.2)'}
              />
            </div>
            <div>Переведите {
              formatMoneyRub(usdToRub(formState.values.usdAmount, usdRate))
            } по реквезитам:</div>
          </>
        )}
      </Form>
      <div className={s.toAcc}>
        <div>Получатель:</div>
        <div>NASH FOND</div>
      </div>
      {account1 && <div className={s.toAcc}>{account1}</div>}
      {account2 && <div className={s.toAcc}>{account2}</div>}
      {account3 && <div className={s.toAcc}>{account3}</div>}
      <div>Выберите платежный протокол RUS:</div>
      <ul>
        <li><a href={'https://koronapay.com/transfers/pages/card2card.aspx'}>Золотая Корона</a></li>
        <li><a href={'http://www.mdm.ru/p2p/'}>Открытие</a></li>
        <li><a href={'https://www.tinkoff.ru/cardtocard/'}>Тинькофф</a></li>
        <li><a href={'https://www.alfaportal.ru/card2card/ptpl/alfaportal/initial.html'}>Альфа-Банк</a></li>
        <li><a href={'https://www.vtb.ru/personal/platezhi-i-perevody/perevody-s-karty-na-kartu/'}>ВТБ</a></li>
      </ul>
      <div>Выбрерите платежный протокол KZ:</div>
      <ul><li><a href={'https://p2p.alfaportal.kz/'}>Альфа-Банк</a></li></ul>
    </div>
  );

  const [tabIndex, setTabIndex] = useState(0);

  const tabs = [{
    name: 'Банковской картой',
    icon: <i className="fas fa-credit-card"/>,
    onClick: () => setTabIndex(0),
  },{
    name: 'По реквизитам',
    icon: <i className="fas fa-university"/>,
    onClick: () => setTabIndex(1),
  }];

  const Tab = ({name, icon, onClick}, index) => (
    <TouchableOpacity
      className={cn(s.modalTab, index===tabIndex && s.active)}
      onClick={onClick}
    >
      <div className={s.menuIcon}>{icon}</div>{name}
    </TouchableOpacity>
  );

  return (
    <Modal
      isOpen={isOpen}
      onClose={onClose}
      className={s.modalWrapper}
      contentStyle={{width: '46rem', maxWidth: '46rem', paddingLeft: 0}}
    >
      <div className={s.modalHat}>
        Пополнить счет
      </div>
      <div className={s.modalMain}>
        <div className={s.modalMenu}>
          {tabs.map(Tab)}
        </div>
        <div
          className={s.depositModal}
        >
          {[
            tabIndex===0 && transactionInfo,
            tabIndex===1 && bank
          ]}
        </div>
      </div>
    </Modal>
  );
};

const ModalWithdraw = ({isOpen, email, onClose}) => (
  <Modal
    isOpen={isOpen}
    onClose={onClose}
  >
    <div className={s.modal}>Чтобы вывести средства со счета, пожалуйста, следуйте инструкции, отправленной на почту:&ensp;
      <span className={s.email}>{email}</span>
    </div>
  </Modal>
);

export const Box = ({style, className, ...props}) => (
  <div className={cn(s.box, className)} style={style}>
    <Text min={0} type={'number'} step={100} className={s.input} {...props}/>
  </div>
);

const Button = ({title, onClick}) => (
  <TouchableOpacity
    onClick={onClick}
    className={s.button}
  >
    {title}
  </TouchableOpacity>
);

export default Deposit;
