import React from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import styles from "./styles.module.scss";

function Side(props) {
  return (
    <Col
      xs={12} lg={6}
      className="flex-column px-1"
      {...props}
    />
  );
}

function About() {
  return (
    <div>
      <Container className={styles.container}>
        <Row>
          <Side>
            <div className={styles.text}>
              Команда трейдеров, аналитиков и юристов с богатым
              опытом в коммерческих и некоммерческих организациях,
              а такжегосударственных финансовых институтах.
            </div>
            <div className={styles.text}>
              Прозрачность инвестирования и маскимальная доступность
              к средствам.
            </div>
          </Side>
          <Side>
            <div className={styles.text}>
              Компания имеет лецензии профессионального участника
              рынка ценных бумаг, выданные  Financial Conduct Authority
              (FCA) UK и регулируется Управлением по финансовому
              регулированию и надзору Великобритании.
            </div>
          </Side>
        </Row>
      </Container>
    </div>
  );
}

export default About;
