import React from 'react';
import s from './s.module.scss';
import cn from 'classnames';
import Popup from "reactjs-popup";
import Wrapper from "../Wrapper";
import TouchableOpacity from "../TouchableOpacity";

const Modal = ({isOpen, onClose=()=>{}, children, className, contentStyle, useDefaultStyling=true}) => {
  return (
    <Popup
      modal
      open={isOpen}
      closeOnDocumentClick={false}
      contentStyle={{
        backgroundColor: 'transparent',
        border: 'none',
        width: '90%',
        maxWidth: '30rem',
        ...contentStyle
      }}
    >
      {(close) => (
        <Wrapper className={cn(useDefaultStyling && s.modal, className)}>
          <Times onClick={() => {
            onClose();
            close();
          }}/>
          {children}
        </Wrapper>
      )}
    </Popup>
  );
};

export const Times = ({onClick}) => (
  <TouchableOpacity onClick={onClick} className={s.times}>
    <i className="fas fa-times"/>
  </TouchableOpacity>
);

export default Modal;