import React, {useEffect} from "react";
import { useHistory } from 'react-router-dom';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
import "./carousel.css";
import styles from './styles.module.scss';
import Button from "../../../../../../common/core/Button";
import cn from 'classnames';
import colors from "../../../../../../common/colors";
import Container from "react-bootstrap/Container";
import Runner from "../../../../Runner";
import {useLocation} from 'react-router-dom';

function Banner() {
  const {pathname} = useLocation();
  // alert(pathname)
  // useEffect(() =?>)
  return (
    <div className={styles.container}>
      <Carousel
        showThumbs={false}
        autoPlay={true}
        showStatus={false}
        stopOnHover={false}
        infiniteLoop={true}
        interval={6000}
      >
        <Slide
          title={'ВЫБЕРИТЕ СВОЙ ИНВЕСТИЦИОННЫЙ ПОРТФЕЛЬ'}
          text={'Мы собрали для вас портфели наиболее прибыльных отраслей'}
          className={styles.slide1}
        />
        <Slide
          title={'КАК ЭТО РАБОТАЕТ'}
          text={'При формировании портфелей используются главные принципы распределения активов'}
          className={styles.slide2}
        />
        <Slide
          title={'ПРЕИМУЩЕСТВА'}
          text={'Создание условий для сохранности средств И Обеспечение высокой и стабильной прибыли'}
          className={styles.slide3}
        />
        <Slide
          title={'ИНВЕСТИЦИОННЫЙ ПОРТФЕЛЬ'}
          text={'МАКСИМАЛЬНАЯ ДОХОДНОСТЬ И ДИВЕРСИФИКАЦИЯ РИСКОВ'}
          className={styles.slide4}
        />
      </Carousel>
      {/*{pathname === '/' && <Runner*/}
      {/*  outer={{*/}
      {/*    // className: styles.runner,*/}
      {/*  }}*/}
      {/*/>}*/}
    </div>
  );
}

function Slide({title, text, link, className}) {
  const history = useHistory();

  return (
    <div
      className={cn(styles.slide, className)}
    >
      <Container className="flex-column justify-content-center align-items-center">
        <div className={styles.title}>{title.toUpperCase()}</div>
        <div className={styles.text}>{text.toUpperCase()}</div>
        <Button
          title="ПОДРОБНЕЕ"
          color={colors.blue}
          onClick={() => history.push(link)}
        />
      </Container>
    </div>
  );
}

export default Banner;
