import React, {useContext} from 'react';
import s from './s.module.scss';
import TouchableOpacity from "../../../common/core/TouchableOpacity";
import formatMoney from "../../../utils/formatMoney";
import DataContext from "../../../contexts/DataContext";

const Bar = ({goToDeposit}) => {
  const {user} = useContext(DataContext);
  const {id:uid, balance, transactionBalance, firstName, secondName} = user.data;
  const allBalance = balance + transactionBalance;

  return (
    <div className={s.container}>
      <div className={s.user}>
        <div className={s.id}><span>ID:</span><span className={s.text}>&ensp;{uid}</span></div>
        {firstName && secondName && <div className={s.name}>{`${firstName} ${secondName}`}</div>}
      </div>
      <TouchableOpacity onClick={goToDeposit} className={s.topup}>ПОПОЛНИТЬ СЧЕТ</TouchableOpacity>
      <div className={s.balance}>
        Баланс:<span className={s.text}>&ensp;{formatMoney(allBalance)}</span>
      </div>
    </div>
  );
};

export default Bar;
