import React from "react";
import Header from "../Header";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Image from "react-bootstrap/Image";
import styles from './styles.module.scss';

function Career() {
  return (
    <>
      <Header
        title={'КАРЬЕРА'}
        className={styles.header}
      />
      <Container className="flex-column pb-4 text-center align-items-center">
        <div className={styles.title}>ХОТИТЕ СТАТЬ ЧАСТЬЮ НАШЕЙ КОМАНДЫ?</div>
        <div className={styles.subtitle}>Еженедельно мы получаем рекомендации специалистов в области торговли на рынке ценных бумаг, крипто валют и рынке Форекс. Мы приверженцы идеи что успех на финансовых рынках достигается не только при получении экономического образования. Статистика успешной, многолетней работы, управления, как своим капиталом, так и доверенными средствами, является основой для начала работы в нашей команде.</div>
        <Row>
          <Question
            icon={require('./images/1.png')}
            text="Вы выпускник высшей школы экономики?"
          />
          <Question
            icon={require('./images/2.png')}
            text="Вы торгуете на рынках более 10-ти лет?"
          />
          <Question
            icon={require('./images/3.png')}
            text="Вы разработали уникальную стратегию, которая на протяжении многих лет приносит вам стабильную прибыль?"
          />
          <Question
            icon={require('./images/4.png')}
            text="Вы готовы показать историю ваших успешных торгов за последние 5 лет?"
          />
        </Row>
        <div className={styles.divider}/>
        <div className={styles.if}>Если на все эти вопросы вы ответили «ДА», тогда возможно ваше  место в нашей команде!</div>
        <div className={styles.send}>Отправьте свое резюме на наш email адрес.</div>
      </Container>
    </>
  );
}

function Question({icon, text}) {
  return (
    <Col xs={12} sm={6} lg={3} className="flex-column align-items-center">
      <Image src={icon} width='30%'/>
      <div className={styles.question}>
        {text}
      </div>
    </Col>
  );
}

export default Career;
