import React, {useRef} from "react";
import Logo from "../Logo";
import {useRouteMatch, useHistory, useLocation, Switch, Route} from 'react-router-dom';
import Container from "react-bootstrap/Container";
import TouchableOpacity from "../../../common/core/TouchableOpacity";
import styles from './styles.module.scss';
import cn from 'classnames';
import useDropDown from "../../../common/hooks/useDropDown";

function Menu() {
  const { path, url } = useRouteMatch();

  const pageTabs = [{
    title: 'ГЛАВНАЯ',
    path: '',
  },{
    title: 'О НАС',
    className: 'd-none d-sm-flex',
    path: 'about',
  },{
    title: 'ФОНД',
    className: 'd-none d-sm-flex',
    dropDown: [{
      name: 'Награды и достижения',
      path: 'awards',
    },{
      name: 'Почему "НАШ ФОНД"?',
      path: 'whyUs',
    },{
      name: 'Преимущества',
      path: 'advantages',
    },{
      name: 'Клиентское соглашение',
    },{
      name: 'Борьба с отмыванием денег',
    },{
      name: 'Политика конфиденциальности',
    },{
      name: 'Экономический календарь',
    }],
  },{
    title: 'УСЛУГИ',
    className: 'd-none d-sm-flex',
    dropDown: [{
      name: 'Персональный консультант',
      path: 'assistant',
    },{
      name: 'Инвестиционный портфель',
      path: 'portfel',
    },{
      name: 'Фонд "НАШ ФОНД"',
      path: 'fund',
    },],
  },{
    title: 'КАРЬЕРА',
    className: 'd-none d-sm-flex',
    path: 'career',
  },{
    title: 'КОНТАКТЫ',
    className: 'd-none d-sm-flex',
    path: 'contacts',
  }];

  const estTabs = [{
    title: 'ОТКРЫТЬ СЧЁТ',
    path: 'register',
  },{
    title: 'ВОЙТИ',
    path: 'login',
  }];

  const containerRef = useRef(null);

  return (
    <div className={styles.holder}>
      <Container className={cn(
        styles.container,
        'justify-content-center',
        'justify-content-lg-between'
      )}>
        <Logo className={cn(styles.logo, 'd-none d-lg-block')}/>
        <div
          className={cn(styles.tabs)}
          ref={containerRef}
        >
          {pageTabs.map((item) => Tab({...item, containerRef}))}
        </div>
        <div className={styles.tabs}>{estTabs.map(Tab)}</div>
      </Container>
    </div>
  );
}

function Tab({title, className, path:pathEnd, dropDown, containerRef}) {
  const history = useHistory();
  const {pathname} = useLocation();

  const isActive = pathname.split('/').pop() === pathEnd;
  // const isActive = Boolean(~pathname.indexOf(pathEnd));

  const ref = useRef(null);
  const dropDownElement = useDropDown({dropDown, ref, containerRef});

  return (
    <>
      {dropDownElement}
      <TouchableOpacity
        ref={ref}
        className={cn(
          styles.tab,
          isActive && styles.active,
          className
        )}
        // todo
        onClick={() => pathEnd !== undefined && history.push(`/${pathEnd}`)}
      >
        {title}
      </TouchableOpacity>
    </>
  );
}

export default Menu;
