import {ProductType} from "../contexts/DataContext";

export function isEmpty(obj) {
  return Object.entries(obj).length === 0;
}

export function portfelsFrom(products) {
  return products.data.filter(({typeId}) => typeId === ProductType.PORTFEL);
}

export function fundsFrom(products) {
  return products.data.filter(({typeId}) => typeId === ProductType.FUND);
}
