import React, {useState} from "react";
import Modal from "../../../../common/core/Modal";
import cn from "classnames";
import s from "./s.module.scss";
import {Form, Text} from "informed";
import CustomButton, {LoadingButtonTitle} from "../../../../common/core/Button";
import {forgotPassword} from "../../../../api/requests";

const ForgotModal = ({initEmail, isOpen, onClose}) => {
  const [success, setSuccess] = useState(false);
  const [email, setEmail] = useState(null);
  const [failed, setFailed] = useState(false);
  const [failText, setFailText] = useState(null);
  const [waiting, setWaiting] = useState(false);

  const resetPassword = async ({email}) => {
    setFailed(false);
    setWaiting(true);

    try {
      await forgotPassword({email});
      setEmail(email);
      setSuccess(true);
    } catch (err) {
      console.log(err);
      setFailed(true);
      setFailText('Произошла ошибка');
    }

    setWaiting(false);
  };

  return (
    <Modal
      isOpen={isOpen}
      onClose={onClose}
      className={cn(s.forgotModal)}
    >
      {success ?
        <div className={s.success}>
          Инструкция по сбросу пароля от Личного кабинета отправлена на указанную почту.
        </div>
        :
        <Form onSubmit={resetPassword} initialValues={{email: initEmail}}>
          {({formState}) => (
            <>
              <div className={s.please}>Введите вашу почту:</div>
              <div className={s.box}>
                <Text className={s.boxInput} field={'email'} type={'email'} required={true}/>
              </div>
              <div className={cn(s.fail, failed && s.showing, s.resetFail)}>{failed && failText}</div>
              <CustomButton
                title={waiting ? <LoadingButtonTitle color={'rgba(255,255,255,0.07)'} text={'ПОДОЖДИТЕ...'}/> : 'СБРОСИТЬ ПАРОЛЬ'}
                disabled={!Boolean(formState.values.email)}
              />
            </>
          )}
        </Form>
      }
    </Modal>
  );
};

export default ForgotModal;
