import {requestSale} from "../../api/requests";

export class ListField {
  constructor({
    listGetter=()=>{},
    creator=()=>{},
    updater=()=>{},
    deleter = () => {},
    getterById = () => {},
    resolver=()=>{},
  }) {
    this.data = [];
    this.isLoaded = false;

    this.load = async () => {
      if (this.isLoaded) return;
      await this._getList();
      this.isLoaded = true;
    };

    this._getList = async () => {
      this.data = await listGetter();
      resolver({...this});
    };

    this.getById = async (id) => {
      // return this.data.find((item) => item.id === id);
      return await getterById(id);
    };

    this.create = async (data) => {
      await creator(data);
      await this._getList();
    };

    this.update = async (id, data) => {
      await updater(id, data);
      await this._getList();
    };

    this.delete = async (id) => {
      await deleter(id);
      await this._getList();
    };
  }

  static default = {
    data: [],
    isLoaded: false,
    load: () => {},
    _getList: () => {},
    getById: () => {},
    create: () => {},
    update: () => {},
    delete: () => {},
  };
}

export class StateField {
  constructor({getter=()=>{}, setter=()=>{}, resolver=()=>{}}) {
    this.data = {};
    this.isLoaded = false;
    this.load = async () => {
      if (this.isLoaded) return;
      await this._get();
      this.isLoaded = true;
    };

    this._get = async () => {
      this.data = await getter();
      resolver({...this});
    };

    this.set = async (data) => {
      await setter(data);
      await this._get();
    };
  }

  static default = {
    data: {},
    isLoaded: false,
    load: () => {},
    _get: () => {},
    set: () => {},
  };
}

export class UserField extends StateField {
  constructor(props) {
    super(props);
    this.requestSale = async (id) => {
      const res = await requestSale(id);
      this._get();
    }
  }
}
