import React, {useEffect, useState} from "react";
import Modal from "../../../../common/core/Modal";
import s from "./s.module.scss";
import FileViewer from "react-file-viewer";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import {Form} from "informed";
import Checkbox from "../../../../common/core/Checkbox";
import TouchableOpacity from "../../../../common/core/TouchableOpacity";
import cn from "classnames";
import CustomButton, {LoadingButtonTitle} from "../../../../common/core/Button";
import './file-viewer.css';
import {registerUser} from "../../../../api/requests";
import Input from "./Input";

export const Register = ({onContinue, initialValues}) => {
  const [isFailed, setFailed] = useState(false);
  const [failText, setFailText] = useState('');

  const [success, setSuccess] = useState(false);
  const [waiting, setWaiting] = useState(false);
  const [email, setEmail] = useState(null);

  const register = async (user) => {
    setWaiting(true);
    setFailed(false);

    const {success, error, data} = await registerUser(user);

    if (success) {
      setEmail(data.user.email);
      setSuccess(true);
    } else if (error === 'user-email-already-exists') {
      setFailText('Пользователь с таким email уже существует');
      setFailed(true);
    } else {
      setFailText('Произошла ошибка, попробуйте еще раз');
      setFailed(true);
    }

    setWaiting(false);
  };

  const [formApi, setFormApi] = useState(null);
  useEffect(() => {
    formApi && formApi.setValues(initialValues);
  }, [initialValues]);

  const [modalType, setModal] = useState(null);

  const modal = modalType && <Modal
    isOpen={true}
    onClose={() => setModal(null)}
    className={s.viewer}
    contentStyle={{
      maxWidth: 'unset',
      width: '80%',
      height: '80%',
    }}
    useDefaultStyling={false}
  >
    {modalType === 'agreement' ?
      <FileViewer
        fileType={'docx'}
        filePath={require('../../../../docs/Client_Agreement.docx')}
      />
      :
      <FileViewer
        fileType={'pdf'}
        filePath={require('../../../../docs/declaration.pdf')}
      />
    }
  </Modal>;

  return success ?
    <RegisterSuccess email={email} onClick={onContinue}/>
    :
    <>
      {modal}
      <Row className={['register', s.row]}>
        <Col xs={10} className={'d-flex justify-content-center'} style={{maxWidth: 400}}>
          <div className={s.container}>
            <Form
              onSubmit={register}
              getApi={setFormApi}
            >
              <Input
                placeholder={'Имя'}
                field={'firstName'}
              />
              <Input
                placeholder={'Фамилия'}
                field={'secondName'}
              />
              <Input
                placeholder={'Email'}
                field={'email'}
                type={'email'}
              />
              <Input
                placeholder={'Телефон'}
                field={'phone'}
                type={'phone'}
              />
              <Input
                placeholder={'Страна'}
                field={'country'}
              />
              <Input
                placeholder={'Адрес'}
                field={'address'}
              />

              <div className={s.check}>
                <Checkbox field={'agreement'} required={true}/>
                <div className={s.text}>
                  Согласен с&nbsp;
                  <TouchableOpacity
                    onClick={() => setModal('agreement')}
                    className={s.link}
                    inline={true}
                  >
                    клиентским соглашением
                  </TouchableOpacity>
                </div>
              </div>

              <div className={s.check}>
                <Checkbox field={'declaration'} required={true}/>
                <div className={s.text}>
                  Согласен с&nbsp;
                  <TouchableOpacity
                    onClick={() => setModal('declaration')}
                    className={s.link}
                    inline={true}
                  >
                    декларацией об общих рисках
                  </TouchableOpacity>
                </div>
              </div>

              <div className={cn(s.fail, isFailed && s.showing)}>
                {isFailed && failText}
              </div>
              <CustomButton
                title={waiting ? <LoadingButtonTitle text={'РЕГИСТРАЦИЯ'}/> : 'РЕГИСТРАЦИЯ'}
                color={'#1ac0ea'}
                borderColor={'#1ac0ea'}
                className={cn(s.button, s.register)}
              />
            </Form>
          </div>
        </Col>
      </Row>
    </>;
};

const RegisterSuccess = ({email, onClick}) => (
  <div className={s.registerSuccess}>
    <div>Поздравляем, вы успешно зарегистрировались!</div>
    <div>Данные для входа отправлены на почту:<br/><span className={s.email}>{email}</span></div>
    <CustomButton
      containerClassName={s.continue}
      title={'ПРОДОЛЖИТЬ'}
      color={'#1ac0ea'}
      borderColor={'#1ac0ea'}
      className={s.button}
      onClick={onClick}
    />
  </div>
);

export default Register;
